.formDivContainer{
    position: relative;
    top: 1rem;
    
    text-align: center;
    justify-content: center;
    align-items: center;

    height: 100%;
    max-height: 500px;
    border-radius: 5px;
    border-color: grey;
    border-width: 1px;
    border-style: double;
    
}

.wrapper hr{
    position: relative;
    
}
.loginForms input{
    border-radius: 5px;
    padding: 0px 15px 0px 15px;
    border-width: 1px;
    width: 80%;
    max-width: 300px;
    font-size: large;
    height: 35px;
    border-style: none;
    background-color: lightgray;
    margin-bottom: 0.5rem;
}
.loginFormsError input{
    margin-top: 0.3rem;
    border-radius: 5px;
    width: 90%;
    max-width: 300px;
    border-width: 2px;
    --main-bg-color : rgb(176, 61, 61);
    border-color: var(--main-bg-color);
    font-size: large;
    height: 35px;
    border-style:solid;
    background-color: lightgray;
}
.spanError{
    font-size:medium;
    color: rgb(176, 61, 61);
}
.loginButton:hover{
    border-width: 1px;
    border-color: black;
}
.loginButton {
    border-radius: 5px;
    border-width: 0px;
    background-color: rgb(77, 90, 190);
    font-size: medium;
    width: 90%;
    max-width: 300px;
    height: 35px;
    color: white;
}.loginButtonGoogle{
    
    width: 90%;
    max-width: 200px;
    height: 30px;
    border-radius: 5px;
    border-style: none;
    background-color: lightgray;
    color: black;
}
#logoPIC{
    display: inline;
  width: 60px;
  height: 60px;
  vertical-align: middle;
}
.logo{
    vertical-align: middle;
    display: inline;
    font-size:40px;
   
    font-family:"Ditted";
}
.logoSpan{
    font-size: 40px;
    vertical-align: middle;
    display: inline;
    color: rgba(190, 37, 37, 0.822);
    font-family:Georgia, 'Times New Roman', Times, serif;

}
.wrapper img{
    vertical-align: middle;
    width: 27px;
    height: 27px;
}
.loginButtonGoogle:hover{
    margin: 0%;
    padding: 0%;
    border-style: solid;
    border-width: 1px;
    border-color: black;
}
a{
    text-decoration: none;
}
@media (max-width:250px) {
    .wrapper img {
        display: none;
    }
}
@font-face {
    font-family: "Dited";
    src: url("../../public/fonts/Dited.otf");
}